.information {
  align-self: stretch;
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.globe-icon {
  position: relative;
  width: 24px;
  height: 24px;
  object-fit: cover;
}
.web {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.wwwabdulbasitcom {
  position: relative;
  line-height: 20px;
  display: inline-block;
  width: 139px;
  flex-shrink: 0;
}
.web-address,
.website {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.web-address {
  flex: 1;
  padding: var(--padding-11xs) 0;
  justify-content: flex-end;
  text-align: right;
  color: var(--main-colour);
}
.website {
  align-self: stretch;
  background-color: var(--color-white);
  justify-content: center;
  gap: var(--gap-19xl);
}
.helloabdulbasitcom {
  position: relative;
  line-height: 20px;
}
.phoneoutline-icon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.website-parent {
  align-self: stretch;
  background-color: var(--color-white);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
  font-size: var(--font-size-sm);
  color: var(--color-gray-100);
}
.information-card-child {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  height: 1px;
  flex-shrink: 0;
}
.ui-designer {
  border-radius: var(--br-xs);
  background-color: var(--color-white);
  box-shadow: var(--dp-umbra);
  display: flex;
  flex-direction: row;
  padding: var(--padding-4xs) var(--padding-base);
  align-items: center;
  justify-content: center;
}
.mobile-app-designer {
  position: relative;
}
.mobile-designer,
.ui-designer-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.ui-designer-parent {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.mobile-designer {
  border-radius: var(--br-xs);
  background-color: var(--color-white);
  box-shadow: var(--dp-umbra);
  padding: var(--padding-4xs) var(--padding-3xs) var(--padding-4xs)
    var(--padding-base);
  justify-content: center;
}
.buttom-chips,
.information-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.buttom-chips {
  align-items: flex-start;
  gap: var(--gap-9xs);
  font-size: var(--font-size-xs);
}
.information-card {
  position: absolute;
  top: 547px;
  left: calc(50% - 207px);
  border-radius: var(--br-base);
  background-color: var(--color-white);
  box-shadow: var(--dp-penumbra);
  width: 414px;
  padding: var(--padding-base) var(--padding-base) var(--padding-5xl);
  box-sizing: border-box;
  align-items: center;
  gap: 32px;
}
.profile-section-card-child {
  align-self: stretch;
  position: relative;
  border-radius: var(--br-base) var(--br-base) 0 0;
  background-color: var(--main-colour);
  height: 100px;
}
.account-icon {
  position: absolute;
  top: 12px;
  left: 11.5px;
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.profile-emoji {
  position: relative;
  border-radius: 46px;
  background-color: #e1dddd;
  width: 64px;
  height: 64px;
}
.abdulbasit {
  position: relative;
  font-weight: 300;
}
.username {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.abdulbasit-idris {
  position: relative;
  font-weight: 600;
}
.name,
.name-and-username {
  align-self: stretch;
  display: flex;
  align-items: center;
  justify-content: center;
}
.name {
  flex-direction: row;
  font-size: var(--font-size-base);
}
.name-and-username {
  flex-direction: column;
}
.nigeria {
  position: absolute;
  top: 0;
  left: 0;
}
.nationality {
  position: relative;
  width: 40px;
  height: 14px;
}
.date-and-location-child,
.date-joined1 {
  position: relative;
  width: 1px;
  height: 14px;
}
.date-joined1 {
  width: 98px;
}
.date-and-location,
.name-username-and-location {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.date-and-location {
  align-self: stretch;
  flex-direction: row;
  font-size: var(--font-size-xs);
}
.name-username-and-location {
  flex-direction: column;
}
.add-user-male {
  position: relative;
  width: 18px;
  height: 18px;
  object-fit: cover;
}
.follow-button,
.message-button {
  border-radius: var(--br-xs);
  border: 1px solid var(--main-colour);
}
.follow-button {
  box-sizing: border-box;
  height: 40px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-11xs) var(--padding-base) var(--padding-11xs)
    var(--padding-xs);
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.message-button {
  padding: var(--padding-2xs) var(--padding-base) var(--padding-2xs)
    var(--padding-5xs);
  gap: var(--gap-xs);
  cursor: pointer;
}
.buttons,
.message-button,
.more-button2 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.more-button2 {
  border-radius: var(--br-xs);
  border: 1px solid var(--main-colour);
  padding: var(--padding-2xs) var(--padding-base) var(--padding-2xs)
    var(--padding-xs);
  gap: var(--gap-5xs);
}
.buttons {
  gap: var(--gap-11xs);
}
.ui-designer-creating {
  position: relative;
  line-height: 20px;
  display: inline-block;
  width: 217px;
  flex-shrink: 0;
}
.description-box,
.profile-section-card {
  display: flex;
  align-items: center;
  justify-content: center;
}
.description-box {
  width: 241px;
  flex-direction: row;
  text-align: center;
  font-size: var(--font-size-xs);
}
.profile-section-card {
  position: absolute;
  top: 68px;
  left: calc(50% - 207px);
  border-radius: var(--br-base);
  background-color: var(--color-white);
  box-shadow: var(--dp-umbra);
  width: 414px;
  flex-direction: column;
  padding: 0 0 var(--padding-5xl);
  box-sizing: border-box;
  gap: 24px;
  font-size: var(--font-size-sm);
}
.iphone-14-pro-max-1 {
  position: relative;
  background-color: var(--color-gainsboro-200);
  width: 100%;
  height: 932px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--main-colour);
  font-family: var(--font-rubik);
}
