@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-rubik: Rubik;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-sm: 14px;
  --font-size-base: 16px;

  /* Colors */
  --color-gainsboro-100: #d7dadb;
  --color-gainsboro-200: #d5d8dd;
  --color-white: #fff;
  --background-colour: #000;
  --color-gray-100: rgba(0, 0, 0, 0.5);
  --main-colour: #25508f;
  --color-darkslateblue-100: rgba(37, 80, 143, 0.3);

  /* Gaps */
  --gap-6xl: 25px;
  --gap-11xl: 30px;
  --gap-xs: 12px;
  --gap-11xs: 2px;
  --gap-5xs: 8px;
  --gap-9xs: 4px;
  --gap-19xl: 38px;

  /* Paddings */
  --padding-base: 16px;
  --padding-8xs: 5px;
  --padding-3xs: 10px;
  --padding-6xs: 7px;
  --padding-5xs: 8px;
  --padding-5xl: 24px;
  --padding-2xs: 11px;
  --padding-xs: 12px;
  --padding-11xs: 2px;
  --padding-4xs: 9px;

  /* border radiuses */
  --br-xs: 12px;
  --br-xl: 20px;
  --br-5xs: 8px;
  --br-base: 16px;

  /* Effects */
  --dp-penumbra: 0px 6px 30px 5px rgba(0, 0, 0, 0.12);
  --dp-umbra: 0px 16px 24px 2px rgba(0, 0, 0, 0.14);
}
