.more-icon {
  width: 18px;
  height: 18px;
  object-fit: cover;
  display: none;
}
.more,
.more-icon {
  position: relative;
}
.message-archive,
.more-button,
.more-button1 {
  border-radius: var(--br-5xs);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.more-button {
  flex: 1;
  background-color: var(--color-white);
  box-shadow: var(--dp-penumbra);
  border: 1px solid var(--main-colour);
  padding: var(--padding-6xs) var(--padding-base) var(--padding-5xs);
  gap: var(--gap-5xs);
}
.message-archive,
.more-button1 {
  background-color: var(--color-gainsboro-100);
}
.more-button1 {
  flex: 1;
  padding: var(--padding-6xs) var(--padding-base) var(--padding-5xs);
  gap: var(--gap-5xs);
  color: #757676;
}
.message-archive {
  align-self: stretch;
  padding: 4px;
}
.ak {
  position: relative;
  font-weight: 500;
}
.initial {
  border-radius: var(--br-xl);
  background-color: var(--color-darkslateblue-100);
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs);
  align-items: center;
  justify-content: center;
}
.hours-ago,
.ismail-muhammad-kois {
  align-self: stretch;
  position: relative;
  font-weight: 500;
}
.hours-ago {
  font-size: var(--font-size-xs);
  font-weight: 300;
  color: var(--color-gray-100);
}
.ismail {
  width: 153px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-11xs);
  color: var(--background-colour);
}
.name-and-profile {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.more-horiz-icon {
  position: relative;
  width: 24px;
  height: 24px;
}
.ismail-chat-header {
  align-self: stretch;
  height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-11xl);
}
.hello-sir-did {
  flex: 1;
  position: relative;
}
.chat-box {
  width: 231px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.double-tick-icon {
  position: relative;
  width: 18px;
  height: 18px;
  object-fit: cover;
}
.double-tick-wrapper {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}
.ismail-chat,
.ismail-chat-box {
  align-self: stretch;
  display: flex;
  justify-content: center;
}
.ismail-chat-box {
  height: 31px;
  flex-direction: row;
  padding: 0 var(--padding-8xs) 0 0;
  box-sizing: border-box;
  align-items: center;
  gap: var(--gap-6xl);
  font-size: var(--font-size-xs);
  color: var(--background-colour);
}
.ismail-chat {
  flex-direction: column;
  align-items: flex-start;
}
.abubakar-musa1,
.yesterday {
  position: relative;
  font-weight: 500;
  display: inline-block;
  width: 153px;
}
.yesterday {
  font-size: var(--font-size-xs);
  font-weight: 300;
  color: var(--color-gray-100);
}
.abubakar {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-11xs);
  color: var(--background-colour);
}
.name-and-profile1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.message-section {
  position: absolute;
  top: 66px;
  left: calc(50% - 207px);
  border-radius: var(--br-xs);
  background-color: var(--color-white);
  width: 414px;
  display: flex;
  flex-direction: column;
  padding: var(--padding-base);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: 48px;
}
.iphone-14-pro-max-2 {
  position: relative;
  background-color: var(--color-gainsboro-200);
  width: 100%;
  height: 932px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--main-colour);
  font-family: var(--font-rubik);
}
